body {
    background-color: rgba(78, 94, 100, 0.747);
    font-family: 'Noto Sans JP', sans-serif;
}


.post {
    width: 550px;
    background-color: rgb(238, 238, 238);
    padding: 10px 0px 10px 10px;
    margin: 50px auto;
}

header {
    display: flex;
    flex-direction: row;
    align-items: center;
}



.avatar img {
    border-radius: 50%;
    width: 60px;
    margin-right: 10px;
    margin-left: 5px;
}

.photo_post img {
    margin-left: 60px;
    width: 80%;
    border-radius: 20px;

}

.info_name {
    font-size: 18px;
    margin-bottom: 5px;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
}

.info_text {
    font-weight: 400;
    margin-bottom: 10px;
    font-size: 15px;

}

.nickname,
.date {
    color: rgb(60, 60, 68);
    font-size: 15px;
    margin-top: 5px;
}



.icons img{
    width: 20px;
    height: 20px;
}

.icons{
display: flex;
justify-content: space-around;
margin-top: 5px;
}

.icons span{
    text-align: center;
    font-size: 14px;
    display: flex;
    align-items: center;
}

